<template>
	<div class="login">
		<div class="login_flex">
			<div class="login_div">
			<i class="iconfont icon-shouji"></i>
			<input type="tel" placeholder="" v-model.trim="phone" maxlength="11" />
		</div>
		<div class="login_div">
			<i class="iconfont icon-mima"></i>
			<input type="password" placeholder="" v-model.trim="password" />
		</div>
		<p class="login_button" @click="login"></p>
		</div>
		<div class="reg">
			<router-link :to="{path: $store.state.routerObj.register }"></router-link>
		</div>
	</div>

</template>

<script>
	import { Toast } from 'mint-ui';
	
	export default {
		name: 'login',
		data() {
			return {
				phone: "",
				password: ""
			}
		},
		mounted() {
			//openid  home
			// openid   openid
			//var wxopenid="oWk-fuP7uqLUJcoX118Mt6oWyFZo";

			// localStorage.setItem("openId" ,"oWk-fuP7uqLUJcoX118Mt6oWyFZo");
			
			var wxopenid = localStorage.getItem("txq_openId");
			var uid = localStorage.getItem('txq_uid');

			if(wxopenid == undefined || wxopenid == '') {
				//  openid  

				// var wxopenid = "oyecBuKbF1seYxcWQOW5HapiKVHw";
				// 

				console.log("");
				location.href = userObj.loginUrl;

			}
			if(uid == null) {
				this.$http.get(this.$store.state.url+"/wx/powderSystem/virUserByOpenId?openid=" + wxopenid).then((response) => {					
					//
					if (response.body.uid == undefined) {
						this.$router.push({path: routerObj.login});
					} else {
						if (response.body.uid != 0) {
							let self = this;
							localStorage.setItem("txq_uid", response.body.uid);
							userObj.nickname=response.body.nickname;
							userObj.uid = response.body.uid;
							userObj.headimgurl=response.body.headimgurl;
							this.$router.push({path: routerObj.home});
						}
					}
					
				});
			} else {
				this.$router.push({path: routerObj.home});
			}

			function isEmptyObject(obj) {
				for(var key in obj) {
					return false;
				}
				return true;
			}
		},
		methods: {
			login() {
//				http://120.24.6.91:8082/wx /powderSystem/wxUserbind
//
//	tel					
//	pwd				
//	openid			OpenId
//	nickname			
//	country			
//	province			
//	city				
//	headimgurl		
//	sex				
//	unionid			()

if (!(/^1[3|4|5|7|8][0-9]\d{4,8}$/.test(this.phone))) {
	Toast({
		message: '',
		position: 'middle',
		duration: 1000
	});
	return false;
}
if (this.password.length < 6) {
	Toast({
		message: '6',
		position: 'middle',
		duration: 1000
	});
	return false;
}

this.$http.get(this.$store.state.url+"/wx/powderSystem/wxUserbind?tel="+this.phone+
"&pwd="+this.password+"&openid=" +localStorage.getItem("txq_openId")+
"&nickname="+ localStorage.getItem("txq_nickname")+
"&country="+ localStorage.getItem("txq_country")+
"&province=" +localStorage.getItem("txq_province")+
"&city="+ localStorage.getItem("txq_city")+
"&headimgurl=" +localStorage.getItem("txq_headimgurl")+
"&sex="+ localStorage.getItem("txq_sex")+
"&unionid="+ localStorage.getItem("txq_unionid")).then((response) =>{
	console.log(response.body)
	console.log('');
	if(response.body.errorcode == 404){
		Toast({
			message: '',
			position: 'middle',
			duration: 1000
		});
	}else if(response.body){
		localStorage.setItem("txq_uid", response.body.Id);
		userObj.uid = response.body.Id;
		this.$router.push({path: routerObj.home});
	}
});

			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../assets/css/var.scss';
	.login {
		display:flex;
		flex-direction: column;
		height:100vh;
		justify-content: space-between;
	.login_flex{
	    margin-top: 15vh;}
		div.login_div {
			height: 52px;
			line-height: 52px;
			width: 70%;
			margin: auto;
			background-color: $whitecolor;
			border: 1px solid $barbg;
			margin-bottom: 15px;
			border-radius: 2px;
			input {
				border: none;
				padding: 10px 0;
				height:50px;
				line-height: 50px;
				margin: 0;
				vertical-align:top;
				box-sizing: border-box;
				width: 70%;
				font-size: $font14;
					 /*background: #00f;*/
			}
			 :-ms-input-placeholder {
				color: $pagebg;
			}
			i {
				font-size: 25px;
				padding: 0 8px;
			}
		}
		p.login_button {
			height: 52px;
			line-height: 52px;
			text-align: center;
			color: $bluecolor;
			background-color: $whitecolor;
			width: 62%;
			margin: auto;
			border-radius: 2px;
			font-size: $font16;
		}
		div.reg {
				height: 50px;
				line-height: 50px;
				text-align: center;
			i {
				color: $bluecolor;
				text-decoration: underline;
			}
		}
	}
</style>


