<template>
  <div class="moreFunction" v-if="flag">

  	<!--  -->
    <mt-swipe :show-indicators="true" :auto="0" :continuous="false" v-if="show == 2">
      <mt-swipe-item>
        <div class="more-flex">
          <div class="more-item" @click="chooseImage">
            <div class="icon">
              <i class="iconfont icon-xiangce"></i>
            </div>
            <p class="detail"></p>
          </div>
          <router-link :to="{path: $store.state.routerObj.banned}" class="more-item">
            <div class="icon">
              <i class="iconfont icon-icon-yxj-silent"></i>
            </div>
            <p class="detail"></p>
          </router-link>
          <div class="more-item" @click="openSignIn">
            <div class="icon">
              <i class="iconfont icon-qiandao"></i>
            </div>
            <p class="detail"></p>
          </div>
          <div class="more-item" @click="openPractice">
            <div class="icon">
              <i class="iconfont icon-lianxi"></i>
            </div>
            <p class="detail"></p>
          </div>
          <router-link :to="{path: $store.state.routerObj.taskEdit}" class="more-item">
            <div class="icon">
              <i class="iconfont icon-renwubianji"></i>
            </div>
            <p class="detail"></p>
          </router-link>
          <router-link :to="{path: $store.state.routerObj.statement}" class="more-item">
            <div class="icon">
              <i class="iconfont icon-baobiao"></i>
            </div>
            <p class="detail"></p>
          </router-link>
          <router-link :to="{path: $store.state.routerObj.inviteFriends}" class="more-item">
            <div class="icon">
              <i class="iconfont icon-yaoqinghaoyou"></i>
            </div>
            <p class="detail"></p>
          </router-link>
          <div class="more-item" @click="openRecorded">
            <div class="icon">
              <i class="iconfont" :class="{'icon-kaishilubo': !$store.state.practiceObj.isRecorded, 'icon-jieshulubo':$store.state.practiceObj.isRecorded }"></i>
            </div>
            <p class="detail">{{recordedText}}</p>
          </div>
        </div>
      </mt-swipe-item>
      <mt-swipe-item>
        <div class="more-flex">
          <router-link :to="{path: $store.state.routerObj.home +'/'+ $store.state.routerObj.homeSet}" class="more-item">
            <div class="icon">
              <i class="iconfont icon-set"></i>
            </div>
            <p class="detail"></p>
          </router-link>
        </div>
      </mt-swipe-item>
    </mt-swipe> 

    <div class="more-flex" v-if="show == 1">
      <div class="more-item" @click="chooseImage">
        <div class="icon">
          <i class="iconfont icon-xiangce"></i>
        </div>
        <p class="detail"></p>
      </div>
      <router-link :to="{path: $store.state.routerObj.inviteFriends}" class="more-item">
        <div class="icon">
          <i class="iconfont icon-yaoqinghaoyou"></i>
        </div>
        <p class="detail"></p>
      </router-link>
      <router-link :to="{path: $store.state.routerObj.home +'/'+ $store.state.routerObj.homeSet}" class="more-item">
        <div class="icon">
          <i class="iconfont icon-set"></i>
        </div>
        <p class="detail"></p>
      </router-link>
     </div>
    <!--  -->
    <selectPracticeTitle :isPractice="emitIsPractice" @on-parctice-change="onParcticeChange"  @sendTitle="_sendTitle"></selectPracticeTitle>
  </div>
</template>

<script>


// 
import selectPracticeTitle from '../functionComponents/selectPracticeTitle'
// 
import { Swipe,SwipeItem,Indicator  } from 'mint-ui'
export default {
  name: 'moreFunction',
  props: ['flag'],
  data () {
    return {
      emitIsPractice: false,
      show: 1,
      recordedText: '',
      // id
      localIds: ''
    }
  },
  mounted() {
    setTimeout(() => {
        this.show = userObj.adminId;
    },500);
  },
  methods: {
    // 
    chooseImage() {
      wx.chooseImage({
        success: (res) => {
            this.localIds = res.localIds; // IDlocalIdimgsrc
            this.syncUpload(this.localIds);
        }
		  });
    },
    // 
    syncUpload(localIds) {
      let localId = localIds.shift();
      wx.uploadImage({
        localId: localId,
        isShowProgressTips: 1,
        success: (res) => {
          var serverId = res.serverId; // ID
          console.log('serverId = ' + serverId);
          Indicator.open('...');
            //serverId
           

            $.ajax({
			        	url: `${this.$store.state.url}/wx/powderSystem/wxPlug/forwardImage`,
			        	type: 'POST',
			        	data: {
			        		serverId: serverId,
			        		tokenId: 1                  
			        	},
			        	success: (res) => {
			        		console.log(res);
                  this.syncUpload(localIds);
                  //  
                  this.$emit('syncUpload',res.src);
                  Indicator.close();
			        	}
			        });
        }
      });
    },
    // 
    openRecorded() {
      let recorded = !this.$store.state.practiceObj.isRecorded;
      this.$store.commit('setRecorded', recorded);
      if (recorded) {
        this.recordedText = '';
        this.$emit('openRecorded', 0);
      } else {
        this.recordedText = '';
        this.$emit('openRecorded', 1);
      }
    },
  	// 
  	openPractice() {
  		this.emitIsPractice = !this.emitIsPractice;
  	},
    onParcticeChange(val) {
      this.emitIsPractice = val;// 4.
    },
    // 
    openSignIn() {
      this.$emit('openSignIn', true);
    },
    // 
    _sendTitle(obj) {
      if (obj) {
          this.$emit('sendTitle', obj);
      }
    },
     // ajax 
    _ajax(url, type, data, cb) {
      this.$http({
        url: url,
        method: type,
        params: data
      })
      .then(res => {
        if(res.status === 200) {
          cb && cb(res);
        }
      }, error => {
        console.log(error);
      });
    },
  },
  components: {
  	mtSwipe: Swipe,
    mtSwipeItem: SwipeItem,
    selectPracticeTitle
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../../assets/css/var.scss";
.moreFunction{
	padding-bottom: 15px;
}
 .more-flex{
	margin-top: 7px;
	display: flex;
	flex-wrap: wrap;
	.more-item{
	  display: block;
	  margin-top: 10px;
	  flex: 0 0 25%;
	  text-align: center;
    color: $txtcolor;
	  .icon{
	    display: inline-block;
	    width: 50px;
	    height: 50px;
	    line-height: 50px;
	    border: 1px solid $bordercolor !important;
	    border-radius: 8px;
	    background: #fff;
	    &:active {
	      background: #f1f1f1;
	    }
	    .iconfont{
	      font-size: 28px;
	      color: $iconcolor;
	    }
	  }
	  .detail{
	    margin-top: 8px;
	  }
	}
}

.mint-swipe{
  height: 200px;
}
</style>



